span{
    color: var(--primary-color);
}

a{
    text-decoration: none;
    text-transform: uppercase;
    font-weight:550; 

}
h1{
    font-size: 56px;
}
h2{
    font-size: 40px;
}
.p,p{
    font-size: 20px;
    color: var(--gray-color);
    line-height: 30pt;
}
li{
    list-style: none;
}
hr{
   width: 5%;
   margin: 25px auto;
   border: 8px dotted var(--brown-color);
   background: transparent;
   border-bottom: none;

}
.app{
    background-color: var(--secondary-color);


    @media screen and (max-width:1080px) {
        p{
            font-size: 19px; 
            line-height: 20pt;
            }
    
            h1{
                font-size: 40px;
            }
            h2{
                font-size: 35px;
            }
            hr{
                width: 15%;
                margin: 10px auto;
            }
    
    }
}
.app__container{
    width: 100%;
    min-height: 100vh;
    padding: 5px 55px;
    display: flex;
    flex-direction: row;
    
    @media screen and (max-width:1080px){
        padding: 15px 20px;
        
    }
}


.app__flex{
    display: flex;
    align-items: center;
    justify-content: center;
}

.app__wrapper{
    flex: 1;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    padding:  4rem 1rem 2rem;

    @media screen and (max-width:1080px){
        padding: 4rem 0rem 2rem;
    }
}

.app__social{
    height: 100vh;
    width: 5%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    position: absolute;
   
    padding: 1rem;

    div{
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: var(--white-color);
        margin: 0.24rem 0;
        border: 1px solid var(--lighGray-color);

        display: flex;
        justify-content: center;
        align-items: center;

        transition: all 0.3s ease-in-out;

        a{
            color: var(--grey-color);
           

            svg{
                width: 15px;
                height: 15px;
                color: var(--grey-color);
            }

         }

         &:hover{
            background-color: var(--primary-color);
            border-color: var(--primary-color);
    
            svg{
                color: var(--white-color);
            }
         }
    }  
}

.App__Content{
    margin-top: 75px;
     justify-content: center;
     @media screen and (max-width:1080px){
        margin-top: 7.5vh;
    }
 
 }

.head-text{
    text-transform: capitalize;
    text-align: center;
    color: var(--black-color);
    font-size: 50px;
}



@media screen and (max-width:1080px){
    .app__social{
        display: none;
    }
}